.checkoutProduct{
    display:flex;
    margin-top:20px;
    margin-bottom:20px;
}
.checkoutProduct_image{
    object-fit:contain;
    width:150px;
    height:150px;
}
.checkoutProduct_info{
    padding-left:20px;
}
.checkoutProduct_info>button{
    margin-top: 20px;
    width:170px;
    background-color: #f0c14b;
    border:1px solid;
    border-color:#a88734 #9c7e31 #846a29;
}

.checkoutProduct_title{
    font-size:20px;
    font-weight:800;
}

.checkoutProduct_rating{
    display:flex;
}